import { Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-reward-points',
  templateUrl: './reward-points.component.html',
  styleUrls: ['./reward-points.component.scss']
})
export class RewardPointsComponent implements OnInit {

  constructor(
    private router:Router
  ) { }

  ngOnInit(): void {
  }

  gotoAddByBill(){
    this.router.navigate(['/cafe/dashboard/management/sales-program/reward-points/by-bill'])
  }

  gotoAddByProduct(){
    this.router.navigate(['/cafe/dashboard/management/sales-program/reward-points/by-product'])
  }

  gotoManage(){
    this.router.navigate(['/cafe/dashboard/management/sales-program/reward-points/manage-point'])
  }
  goBack() {
    this.router.navigate(['/cafe/dashboard/management/sales-program'])
  }
}
