import { NgModule } from '@angular/core';
import { RewardPointsComponent } from './reward-points.component';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router'; 
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module';

const routes: Routes = [
  {
    path: '',
    component: RewardPointsComponent
  },
  {
    path: 'by-bill',
    loadChildren: () => import('./by-bill/by-bill.module').then(m => m.ByBillModule)
  } ,
  {
    path: 'by-product',
    loadChildren: () => import('./by-product/by-product.module').then(m => m.ByProductModule)
  } ,
  {
    path: 'manage-point',
    loadChildren: () => import('./manage-point/manage-point.module').then(m => m.ManagePointModule)
  } 
]


@NgModule({
  declarations: [RewardPointsComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    TranslateModule
  ]
})
export class RewardPointsModule { }
